/* Ogólne ustawienia */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  color: #333;
  box-sizing: border-box;
}

/* Kontener dla formularza */
.recipe-form-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background: white;
  width: 90%;
  max-width: 500px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  box-sizing: border-box;
}

/* Przycisk zamknięcia formularza */
.close-button {
  position: absolute;
  top: 15px; /* Zwiększony margines, aby nie było ucięcia */
  right: 15px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #333;
  z-index: 10; /* Upewniamy się, że przycisk jest nad treścią */
}

.close-button:hover {
  color: #ff0000;
}

/* Formularz */
.recipe-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.recipe-form label {
  font-weight: bold;
  margin-bottom: 5px;
}

.recipe-form input,
.recipe-form textarea {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
}

.recipe-form textarea {
  resize: vertical;
  min-height: 80px;
}

.recipe-form button {
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.recipe-form button:hover {
  background-color: #45a049;
}

/* Stylowanie listy przepisów */
.recipe-list-container {
  background: white;
  margin: 20px auto;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-width: 800px;
}

.recipe-list-container ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.recipe-list-container li {
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.recipe-list-container li:last-child {
  border-bottom: none;
}

.recipe-title {
  font-size: 16px;
  color: #333;
  text-decoration: none;
  cursor: pointer;
  background: none;
  border: none;
}

.recipe-title:hover {
  text-decoration: underline;
}

.delete-button {
  background-color: #ff4d4d;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.delete-button:hover {
  background-color: #e60000;
}

/* Responsywność */
@media (max-width: 768px) {
  .recipe-form-container {
    width: 95%;
  }

  .recipe-list-container {
    margin: 10px auto;
    padding: 10px;
  }

  .recipe-form textarea {
    min-height: 60px;
  }
}

@media (max-height: 600px) {
  .recipe-form-container {
    top: 5%;
    transform: translate(-50%, 0);
    max-height: 90%;
    overflow-y: auto;
  }

  .close-button {
    top: 10px; /* Dostosowanie pozycji w przypadku małych ekranów */
    right: 10px;
  }
}
